"use client";
import React, { useRef, useEffect } from "react";
import { gsap } from "gsap";

function Abouthome() {
  const sectionRef = useRef<HTMLElement>(null);
  const headingRef = useRef<HTMLHeadingElement>(null);
  const paragraphRefs = useRef<(HTMLParagraphElement | null)[]>([]);

  useEffect(() => {
    // Set initial states
    gsap.set(headingRef.current, { opacity: 0, y: 50 });
    gsap.set(paragraphRefs.current, { opacity: 0, y: 30 });

    // Animation timeline
    const tl = gsap.timeline({ defaults: { ease: "power3.out" } });
    tl.to(headingRef.current, {
      opacity: 1,
      y: 0,
      duration: 1,
    }).to(
      paragraphRefs.current,
      {
        opacity: 1,
        y: 0,
        duration: 0.8,
        stagger: 0.2,
      },
      "-=0.5"
    );

    // Cleanup
    return () => {
      tl.kill();
    };
  }, []);

  return (
    <section
      ref={sectionRef}
      className="relative py-16 px-4 sm:px-8 bg-black text-white flex flex-col items-center overflow-hidden"
    >
      {/* Subtle background accent */}
      <div className="absolute inset-0 bg-gradient-to-r from-transparent via-[#c5fb45]/5 to-transparent opacity-10" />

      <h2
        ref={headingRef}
        className="text-4xl sm:text-5xl font-bold text-center mb-12 text-[#c5fb45] tracking-tight relative
          before:content-[''] before:absolute before:bottom-[-0.5rem] before:left-1/2 before:-translate-x-1/2 
          before:w-24 before:h-1 before:bg-[#c5fb45]/80 before:transition-all hover:before:w-32
          font-['Playfair_Display',_serif]"
      >
        About Krunal's Academy
      </h2>

      <div className="max-w-3xl w-full text-center space-y-6 z-10">
        <p
          ref={(el) => {
            paragraphRefs.current[0] = el;
          }}
          className="text-lg sm:text-xl text-gray-300 leading-relaxed transition-all duration-300 hover:text-[#a3e635]"
        >
          Founded in 2010, Krunal's Academy has been at the forefront of beauty education, empowering
          thousands of students to become successful professionals in the industry.
        </p>
        <p
          ref={(el) => {
            paragraphRefs.current[1] = el;
          }}
          className="text-lg sm:text-xl text-gray-300 leading-relaxed transition-all duration-300 hover:text-[#a3e635]"
        >
          Our mission is to provide world-class training in hair, skin, makeup, and nail artistry,
          combining traditional techniques with modern innovations.
        </p>
        <p
          ref={(el) => {
            paragraphRefs.current[2] = el;
          }}
          className="text-lg sm:text-xl text-gray-300 leading-relaxed transition-all duration-300 hover:text-[#a3e635]"
        >
          With state-of-the-art facilities and industry-expert instructors, we create an environment
          where creativity and professionalism thrive.
        </p>
      </div>

      {/* Decorative glow */}
      <div className="mt-12 w-20 h-20 rounded-full bg-[#c5fb45]/10 blur-3xl animate-pulse" />
    </section>
  );
}

export default Abouthome;